.quick-view {
  width: 100%;
  height: 70px;
  background-color: var(--color-orange);
  cursor: pointer;
  letter-spacing: -.04em;
  text-transform: uppercase;
  transition: transform var(--transition-rule);
  justify-content: center;
  align-items: center;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  display: flex;
  position: absolute;
